<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Students</h4>
            </li>
            <li v-for="s in students" v-bind:key="s.id" class="collection-item">
                <div class="chip">{{s.address}}</div>
                {{s.student_id}}:{{s.student_name}}

                <router-link class="secondary-content" v-bind:to="{name:'view-student',params:{student_id:s.student_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>

            </li>

        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'dashboard',
    data() {
        return {
            students: []
        }
    },
    created() {
        db.collection('student').orderBy('address').get().then
            (querySnapshot => {
                querySnapshot.forEach(doc => {
                    // console.log(doc.id);
                    const data = {
                        'id': doc.id,
                        'student_id': doc.data().student_id,
                        'student_name': doc.data().student_name,
                        'address': doc.data().address,
                        'contact_no': doc.data().contact_no,
                        'email': doc.data().email
                    }
                    this.students.push(data)
                });
            })
    }
}
</script>



