<template>
    <div id="edit-student">
        <h3>Edit Student</h3>
        <div class="row">
            <form @submit.prevent="updateStudent()" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="student_id" disabled required>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="student_name" required>

                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="address" required>

                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="contact_no" required>

                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="email" required>

                    </div>
                </div>

                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from "./firebaseinit"
export default {
    name: 'edit-student',
    data() {
        return {
            student_id: null,
            student_name: null,
            address: null,
            contact_no: null,
            email: null
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('student').where('student_id',
            '==', to.params.student_id).get()
            .then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    next(vm => {
                        vm.student_id = doc.data().student_id
                        vm.student_name = doc.data().student_name
                        vm.address = doc.data().address
                        vm.contact_no = doc.data().contact_no
                        vm.email = doc.data().email
                    })
                })
            })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('student').where('student_id',
                '==', this.$route.params.student_id).get()
                .then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        this.student_id = doc.data().student_id
                        this.student_name = doc.data().student_name
                        this.address = doc.data().address
                        this.contact_no = doc.data().contact_no
                        this.email = doc.data().email
                    })
                })
        },
        updateStudent() {
            db.collection('student').where('student_id',
                '==', this.$route.params.student_id).get()
                .then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        doc.ref.update({
                            student_id: this.student_id,
                            student_name: this.student_name,
                            address: this.address,
                            contact_no: this.contact_no,
                            email: this.email
                        })
                            .then(() => {
                                this.$router.push({
                                    name: 'view-student',
                                    params: { student_id: this.student_id }
                                });
                            })

                    })
                })

        }
    }
}
</script>



