<template>
    <div id="view-student">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{student_name}}
                </h4>
            </li>
            <li class="collection-item">Student ID : {{student_id}}</li>
            <li class="collection-item">Name: {{student_name}}</li>
            <li class="collection-item">Address: {{address}}</li>
            <li class="collection-item">Email: {{email}}</li>
            <li class="collection-item">Contact No: {{contact_no}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteStudent()" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{ name: 'edit-student', params: { student_id: student_id }}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from "./firebaseinit"
export default {
    name: 'view-student',
    data() {
        return {
            student_id: null,
            student_name: null,
            address: null,
            contact_no: null,
            email: null
        }
    },

    beforeRouteEnter(to, from, next) {
        db.collection('student').where('student_id',
            '==', to.params.student_id).get()
            .then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    next(vm => {
                        vm.student_id = doc.data().student_id
                        vm.student_name = doc.data().student_name
                        vm.address = doc.data().address
                        vm.contact_no = doc.data().contact_no
                        vm.email = doc.data().email
                    })
                })
            })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('student').where('student_id',
                '==', this.$route.params.student_id).get()
                .then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        this.student_id = doc.data().student_id
                        this.student_name = doc.data().student_name
                        this.address = doc.data().address
                        this.contact_no = doc.data().contact_no
                        this.email = doc.data().email
                    })
                })
        },
        deleteStudent() {
            if (confirm('Are you sure?')) {
                db.collection('student').where('student_id',
                    '==', this.$route.params.student_id).get()
                    .then((querySnapshot) => {
                        querySnapshot.forEach((doc) => {
                            doc.ref.delete();
                            this.$router.push('/')
                        })
                    })
            }
        }
    }


    // beforeRouterEnter(to, from, next) {
    //     db.collection('student').where('student_id',
    //         '==', to.params.student_id).get()
    //         .then((querySnapshot) => {
    //             querySnapshot.forEach((doc) => {
    //                 next(vm => {
    //                     vm.student_id = doc.data().student_id
    //                     vm.student_name = doc.data().student_name
    //                     vm.address = doc.data().address
    //                     vm.contact_no = doc.data().contact_no
    //                     vm.email = doc.data().email
    //                 })
    //             })
    //         })
    // },
    // watch: {
    //     '$route': 'fetchData'
    // },
    // methods: {
    //         fetchData() {
    //         db.collection('student').where('student_id', '==',
    //             this.$route.params.student_id).get()
    //             .then((querySnapshot) => {
    //                 querySnapshot.forEach(doc => {
    //                     this.student_id = doc.data().student_id
    //                     this.student_name = doc.data().student_name
    //                     this.address = doc.data().address
    //                     this.contact_no = doc.data().contact_no
    //                     this.email = doc.data().email
    //                 })
    //             })
    //     },
    //     deleteStudent() {
    //         if (confirm('Are You Sure?')) {
    //             db.collection('student')
    //                 .where('student_id', '==',
    //                 this.$route.params.student_id).get()
    //                 .then((querySnapshot) => {
    //                     querySnapshot.forEach(doc => {
    //                         doc.ref.delete();
    //                         this.$router.push('/')
    //                     })
    //                 })
    //         }
    //     }
    // }
}
</script>



